import React,{ useState }from 'react';
import Dropzone from 'react-dropzone';
import { TfiPlus } from "react-icons/tfi";
import Axios from 'axios';

function FileUpload(props) {


    const [Images, setImages] = useState([]) //   


const onDropHandler = (files) => {

    let formData = new FormData();

        const config = {
            headers: { 'content-type': 'multipart/form-data' }
        }
        formData.append("file", files[0])
        //save the Image we chose inside the Node Server 
        Axios.post('/api/product/image', formData, config)
            .then(response => {
                if (response.data.success) {

                    setImages([...Images, response.data.filePath])
                    props.refreshFunction([...Images, response.data.filePath])
                    // UploadProductPage   
                    //  refreshFunction  

                } else {
                    alert('  ')
                }
            })
}


const onDelete = (image) => {
    const CurrentIndex = Images.indexOf(image); // indexof  

    let newImages = [...Images] //     
    newImages.splice(CurrentIndex, 1) // splice    

    setImages(newImages); // useState    
    props.refreshFunction(newImages); // ploadProductPage   
}


  return (
    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <Dropzone
            onDrop={onDropHandler}
            // multiple={false}
            // maxSize={800000000}
        >
            {({ getRootProps, getInputProps }) => (
                <div style={{
                    width: '300px', height: '240px', border: '1px solid lightgray',
                    display: 'flex', alignItems: 'center', justifyContent: 'center'
                }}
                    {...getRootProps()}
                >
                    {/* {console.log('getRootProps', { ...getRootProps() })}
                    {console.log('getInputProps', { ...getInputProps() })} */}
                    <input {...getInputProps()} />
                  
                    <TfiPlus style={{ color:'#333',fontSize: '3rem' }}  />

                </div>
            )}
        </Dropzone>
        <div style={{ display: 'flex', width: '350px', height: '240px', overflowX: 'scroll' }}>

        {Images.map((image, index) => (
            <div 
                key={index}
                onClick={() => onDelete(image)} 
            >
                <img style={{ minWidth: '300px', width: '300px', height: '240px' }} src={`${image}`} alt='' />
            </div>
        ))}


            </div>
        
    </div>
  )
}

export default FileUpload