import React,{ useState } from 'react';
import { Form, Input} from 'antd';
import FileUpload from '../../utils/FileUpload';
import { useNavigate } from 'react-router-dom';
import Axios from 'axios';

const { TextArea } = Input;



const Continents = [
    { key: 1, value: "france" },
    { key: 2, value: "germany" },
    { key: 3, value: "italy" },
    { key: 4, value: "america" },
    { key: 5, value: "korea" },
    { key: 6, value: "spain" },
    { key: 7, value: "united kingdom" }
]




function UploadProductPage(props) {


     //  
 const navigate = useNavigate();



    const [Title, setTitle] = useState("")
    const [Description, setDescription] = useState("")
    const [Price, setPrice] = useState(0)
    const [Continent, setContinent] = useState(1)
    const [Images, setImages] = useState([])


    //  
    const onTitleChange = (e) => {

        setTitle(e.currentTarget.value)

    }

    //   

    const onDescriptionChange = (e) => {

        setDescription(e.currentTarget.value)

    }

//   

    const onPriceChange = (e) => {

        setPrice(e.currentTarget.value)

    }


    //   

    const onContinentsSelectChange = (e) => {

        setContinent(e.currentTarget.value)
        
    }


    const updateImages = (newImages) => {


        setImages(newImages)

    }


    //    

    const submitHandler = (e) => {

        e.preventDefault();

        // 


        if (!Title|| !Description || !Price || !Continent|| !Images) {
            return alert('  !!')
        }

        //     request 


        const variables = {

            //    id
            // hoc auth.js   user  
            writer: props.user.userData._id,
            title: Title,
            description: Description,
            price: Price,
            images: Images,
            continents: Continent,
        }



        Axios.post('/api/product', variables)

        .then(response => {
            if(response.data.success){  
                alert('  ')
                navigate('/')// 


            }else{
                alert('  ')
            }
        })
    }

  return (
    <div style={{maxWidth:'700px', margin:'2rem auto'}}>
        <div style={{ textAlign:'center', marginBottom:'2rem'}}>

                <h2 style={{marginBottom:'30px'}}>
                Product Upload 
                </h2>
                <FileUpload refreshFunction={updateImages} />
                {/*    Input*/}

                <Form
                    onSubmit={submitHandler}
                >
                    <br />
                    <br />
                    <label>product name</label>
                    <Input  
                        onChange={onTitleChange} 
                        value={Title} 
                    /> 
                            
                    <br />
                    <br />
                    <label>description</label>
                    <TextArea 
                        onChange={onDescriptionChange}
                        value={Description}
                    />
                    <br />
                    <br />
                    <label>price</label>
                    <Input
                         onChange={onPriceChange}
                         value={Price}
                         type="number"
                    />
                    <br />
                    <br />
                    <label>country of manufacture </label>
                    <select onChange={onContinentsSelectChange} value={Continent}>

                    {Continents.map(item => (
                        <option key={item.key} value={item.key}>
                            {item.value}
                        </option>
                        
                    ))}

                        
                    
                    </select>
                    <br />
                    <br />
                    <button
                        onClick={submitHandler}
                        style={{ backgroundColor:'#333', border:'0', color:'white',lineHeight:'30px', lineheight:'30px', padding:'5px 30px'}}
                    >
                        Submit
                    </button>

                </Form>

              

        </div>
    </div>
    
  )
}

export default UploadProductPage